.container {
    padding: 0 20px;
    margin-left: auto;
    margin-right: auto;
    @media screen and (max-width: 479px) {
        width: 100%;
    }
    @media screen and (min-width: 480px) {
        width: 460px;
    }
    @media screen and (min-width: 768px) {
        width: 720px;
        padding: 0 10px;
    }
    @media screen and (min-width: 1280px) {
        width: 1080px;
        padding: 0 15px;
    }
}